import React, {Component} from "react";

export default class MultiCartItemIconCellComponent extends Component {
    constructor (props) {
        super(props);
        this.cellTypes = {
            ICON: 'opl-checkout__icon__cell',
            IMAGE: 'opl-checkout__image__cell'
        };

        this.productIcons = {
            SIM: 'sim-3',
            VAS: 'funpack-2 g-icon--s',
            GADGET: 'gift'
        }
    }

    render() {
        let iconClass,
            cellType;

        if (['SIM', 'VAS', 'GADGET', 'DATA', 'BROADBAND', 'TV', 'MOBILE_VOICE', 'VOIP'].indexOf(this.props.entryType) !== -1) {
            iconClass = this.props.icon || this.productIcons[this.props.entryType];
            cellType = this.cellTypes.ICON;
        } else if (this.props.entryType === 'TERMINAL') {
            cellType = this.cellTypes.IMAGE;
        }
        let verticalAlignClass=" u-vertical-top  u-padding-top-l";
        if(document.getElementById("ora-product-list-header-component")) {//is LP
            verticalAlignClass = " u-vertical-middle ";
        }
        return (
            <td rowSpan={this.props.rowSpan || undefined} className={ cellType + (this.props.entryType!=='VAS' ? verticalAlignClass   : "")}>
                { cellType === this.cellTypes.ICON &&
                    <span className={'g-icon g-icon--only g-icon--' + iconClass } aria-hidden="true"></span>
                }
                { cellType === this.cellTypes.IMAGE &&
                    <img src={this.props.image} className="opl-checkout__image--phone" alt={this.props.terminalName}/>
                }
            </td>
        )
    }
}